import React from 'react'
import * as PropTypes from 'prop-types'
import classes from './AmountPostfix.module.css'

function AmountPostfix({ value, className }) {
  return (
    <div className={`${classes.Postfix} ${className || ''}`}>
      {value || 0} шт.
    </div>
  )
}

AmountPostfix.propTypes = {
  value: PropTypes.any,
  className: PropTypes.string
}

export default AmountPostfix
